.App {
  text-align: center;
  width: 100%;
  height: 100vh;
}

.pokemonCardContainer{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
  gap: 20px;
  margin-top: 20px;

}

.btn {
  padding: 30px 0;
  display:flex;
  justify-content: center;

  gap: 20px;

}

.btn button {
  padding: 13px 32px;
  background-color: #5e70ff;
  box-shadow: 3px 7px 15px -5px #777777;
  border-radius: 10px;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
}

.btn button:hover {
  transform: translateY(5px);
}